import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import SwiperClass, { SwiperOptions } from 'swiper'
import '@/styles/swiper.css'

@Component({
  name: 'ImagePreviewDialog',
  components: {
    Swiper,
    SwiperSlide
  }
})
export default class extends Vue {
  @Prop() private visible!:Boolean
  @Prop() private imagesList!: string[]
  @Prop() private selIndex!: number

  private swiperOptions: SwiperOptions = {
    zoom: true,
    initialSlide: this.selIndex,
    slidesPerView: 1,
    // spaceBetween: 4,
    // direction: 'horizontal',
    // slidesPerGroup: 3,
    // loop: true,
    // loopFillGroupWithBlank: true,
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev'
    }
  }

  // ui
  private handleClose(done: any) {
    // this.$emit('update:visible', false)
    this.$emit('closed')
  }

