// import request from '@/utils/request'
import { IJsonCityEntity, IBaseCityListEntity } from './types'
const JsonCitys:IJsonCityEntity[] = require('./json/china-citys.json')

/// /////////////////////////////////////////////////////////////

// 城市
export function getCitys():IBaseCityListEntity[] {
  let citysData: IBaseCityListEntity[] = []

  function mapJsonCitys(citys:IJsonCityEntity[]) {
    return citys.map((item) => {
      let newItem:IBaseCityListEntity = {
        label: item.name,
        value: item.code
      }
      // if (item.areaList !== undefined && item.level <= 1) {
      //   newItem.children = mapJsonCitys(item.areaList)
      // }
      return newItem
    })
  }

  citysData = mapJsonCitys(JsonCitys)

  return citysData
}
