



































































































































































import { Component, Vue } from 'vue-property-decorator'
import { IManageUserPageListItemEntity } from '@/api/types'
import { getUserDetail, setUserFreeze, setUserUnfreeze } from '@/api/manage-users'
import PageTopBar from '@/components/PageTopBar/page-top-bar.vue'
import { getSexTitle, getUserStatusTitle, formatDate,getUserMarriageTitle } from '@/utils/format'
import MessagesListWidget from './components/messages-list.vue'
import VideosListWidget from './components/videos-list.vue'
import FreezeDialog from './components/freeze-dialog.vue'
import UnFreezeDialog from './components/unfreeze-dialog.vue'

@Component({
  name: 'ManageUserDetailPage',
  components: {
    PageTopBar,
    MessagesListWidget,
    VideosListWidget,
    FreezeDialog,
    UnFreezeDialog
  }
})
export default class extends Vue {
  private userDetail:IManageUserPageListItemEntity = {}

  private getSexTitle = getSexTitle
  private getUserStatusTitle = getUserStatusTitle
  private getUserMarriageTitle = getUserMarriageTitle
  private formatDate = formatDate

  private activeName:string = 'messages'
  private uid: string = ''
  private dialogVisible:boolean = false
  private unFreezeDialogVisible:boolean = false

  public constructor() {
    super()
    this.uid = this.$route.params.id
  }

  mounted() {
    this.ajaxUserDetail(this.uid)
  }

  beforeDestroy() {
  }

  private async ajaxUserDetail(uid: string) {
    const response = await getUserDetail(uid, {})
    this.userDetail = response as IManageUserPageListItemEntity
  }

  handleTab(tab: any, event: any) {
    // console.log(tab, event)
  }

  private async handleFreeze() {
    this.dialogVisible = true
  }

  private async handleUnFreeze() {
    this.unFreezeDialogVisible = true
  }
}
