






























import { Component, Prop, Vue, Watch } from 'vue-property-decorator'

export interface IRadioTabsEntity {
  code: string;
  title: string;
  value: string;
}

@Component({
  name: 'RadioTabsWidget'
})
export default class extends Vue {
  @Prop({ required: true }) private items!: IRadioTabsEntity;
  @Prop({ required: true }) private selVal!: string;

  private radioGroupSelVal: string = this.selVal

  // @Watch('selVal', { deep: true })
  @Watch('selVal')
  private onSelValChange(value: string) {
    this.radioGroupSelVal = value
  }

  private handleChange(value: string) {
    this.$emit('update:selVal', value)
    this.$emit('change', value)
  }
}
