


























































































































































































































import { Component, Vue } from 'vue-property-decorator'
import { IManageMessagePageListEntity, IManageMessagePageListItemEntity } from '@/api/types'
import { getMessagesPageList, setMessagesPass, setMessagesReject, setMessagesRevocation } from '@/api/manage-messages'
import { formatDate, formatDateDot, getMessageStatusTitle, getMessageStatusType, getTopStatusTitle } from '@/utils/format'
import Pagination from '@/components/Pagination/index.vue'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import '@/styles/swiper.css'
import ImagePreviewDialog from './components/image-preview-dialog.vue'
import NothingWidget from '@/components/Nothing/index.vue'
import RadioTabsWidget from '@/components/RadioTabs/index.vue'
import { ElForm } from 'element-ui/types/form'

export interface ISearchRequestEntity {
    id: string,
    pagesize: number,
    page: number,
    sd: number,
    ed: number,
    state: string,
    sortProp: string,
    sortOrder: string
}

@Component({
  name: 'AuditMessagesList',
  components: {
    Pagination,
    Swiper,
    SwiperSlide,
    ImagePreviewDialog,
    NothingWidget,
    RadioTabsWidget
  }
})
export default class extends Vue {
  private searchData:ISearchRequestEntity = {
    id: '',
    pagesize: 10,
    page: 1,
    sd: -1,
    ed: -1,
    state: '',
    sortProp: 'createDate',
    sortOrder: 'descending'
  }
  private loading:boolean = false
  private dateRange: Date[] = []
  private dataTable: IManageMessagePageListEntity = {
    items: []
  }
  private currentDialogComponent: string = ''
  private selImagesList: string[] = []
  private selImageIndex: number = 0
  private dialogVisible:boolean = false
  private chkSelectedValue:boolean = false

  private formRules = {
    id: [{ min: 0, max: 15, message: '长度在 0 到 15 个字符', trigger: 'blur' }]
  }

  private types = ["全部","已审核","未审核"]

  private formatDate = formatDate
  private formatDateDot = formatDateDot
  private getMessageStatusTitle = getMessageStatusTitle
  private getTopStatusTitle = getTopStatusTitle
  private getMessageStatusType = getMessageStatusType

  private swiperOptions = {
    slidesPerView: 3,
    spaceBetween: 4,
    // direction: 'horizontal',
    // slidesPerGroup: 3,
    // loop: true,
    // loopFillGroupWithBlank: true,
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev'
    }
  }

  // 是否隐藏操作按钮
  get isHideBatchOptButtons(): boolean {
    if (this.searchData.state == '2' || this.searchData.state == '0') {
      return true;
    }else{
      return false;
    }
  }

  get isHideNotPass(): boolean {
    if(this.searchData.state == '0') {
      return true;
    }
    return false;
  }

  private async getList() {
    this.doSearch()
  }

  private async doSearch() {
    (this.$refs.ruleForm as ElForm).validate(async(valid: boolean) => {
      if (valid) {
        this.loading = true
        const response = await getMessagesPageList({
          ...this.searchData
        })
        this.dataTable = response as IManageMessagePageListEntity
        this.chkSelectedValue = false
        this.loading = false
      } else {
        return false
      }
    })

    // console.log(this.searchData)
  }

  // tab页切换
  private handleTabClick(val:any) {
    this.searchData.page = 1
    this.doSearch()
  }

  // 图片预览
  private handleImagePreviewDialog(row:IManageMessagePageListItemEntity, index: number) {
    this.selImagesList = row.imageContent === undefined ? [] : row.imageContent
    this.selImageIndex = index
    this.currentDialogComponent = 'ImagePreviewDialog'
    this.dialogVisible = true
    // const preView = this.$refs['imagePreviewDialog'] as ImagePreviewDialog
    // preView.setPreviewIndex(index)
  }
  // 图片预览删除
  private handleImagePreviewDialogClosed() {
    this.currentDialogComponent = ''
    this.dialogVisible = false
  }

  // 用户详情
  private async handleShowDetail(userId: string) {
    this.$router.push({ path: `/manage-users/detail/${userId}` })
  }

  // 全选
  private handleChkAll(val:boolean) {
    // console.log(val)
    if (this.dataTable.items !== undefined) {
      this.dataTable.items = this.dataTable.items.map(item => {
        return {
          ...item,
          chkSelected: item.chkSelected === undefined ? true : val
          // chkSelected: item.chkSelected === undefined ? true : !item.chkSelected
        }
      })
    }
  }

  // 批量

  private handlePassAll() {
    if (this.dataTable.items !== undefined) {
      const finds = this.dataTable.items.filter(item => {
        return (item.chkSelected === true)
      })
      if (finds.length === 0) {
        this.$message({
          message: '请选择后操作！',
          type: 'error'
        })
        return
      }
      const ids = finds.map(item => item.id)

      this.$confirm('确定要批量通过审核?', '', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'success'
      }).then(async() => {
        await setMessagesPass(ids)
        await this.doSearch()
      }).catch(() => {})
    }
  }

  private handleRejectAll() {
    if (this.dataTable.items !== undefined) {
      const finds = this.dataTable.items.filter(item => {
        return (item.chkSelected === true)
      })
      if (finds.length === 0) {
        this.$message({
          message: '请选择后操作！',
          type: 'error'
        })
        return
      }
      const ids = finds.map(item => item.id)

      this.$confirm('确定要批量拒绝审核?', '', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'error'
      }).then(async() => {
        await setMessagesReject(ids)
        await this.doSearch()
      }).catch(() => {})
    }
  }

  // 单条

  private handlePass(ids: number[]) {
    this.$confirm('确定要通过审核?', '', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'success'
    }).then(async() => {
      await setMessagesPass(ids)
      await this.doSearch()
    }).catch(() => {})
  }

  private handleReject(ids: number[]) {
    this.$confirm('确定要拒绝审核?', '', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'error'
    }).then(async() => {
      await setMessagesReject(ids)
      await this.doSearch()
    }).catch(() => {})
  }

  private handleRevocation(ids: number[]) {
    this.$confirm('确定要撤销审核?', '', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(async() => {
      await setMessagesRevocation(ids)
      await this.doSearch()
    }).catch(() => {})
  }

  mounted() {
    this.doSearch()
  }

  beforeDestroy() {
  }
}
