const dataFormat = require('date-format')

// 性别
export function getSexTitle(val: string) {
  switch (val) {
    case 'man':
      return '男'
    case 'woman':
      return '女'
    default:
      return '未知'
  }
}

// 用户状态
export function getUserStatusTitle(val: string) {
  switch (val) {
    case '1':
      return '正常'
    case '2':
      return '冻结'
    default:
      return '未知'
  }
}

export function getUserMarriageTitle(val:number) {
  switch (val) {
    case 0:
      return '未婚'
    case 1:
      return '已婚'
    default:
      return '未知'
  }
}

