



















































import { Component, Vue, Prop } from 'vue-property-decorator'
import { setUserUnfreeze } from '@/api/manage-users'
import { ElForm } from 'element-ui/types/form'

export interface IUserUnFreezeRequest {
    userId: number;
    reasonsForThawing?: string;
}

@Component({
  name: 'UserUnFreezeDialog'
})
export default class extends Vue {
  @Prop() private visible!:Boolean
  @Prop() private userid!:number

  private unfreezeRequest:IUserUnFreezeRequest = {
    userId: this.userid,
    reasonsForThawing: ''
  }

  private formRules = {
    frozenRemarks: [{ required: true, message: '内容不能为空', trigger: 'blur' }]
  }

  // ui

  private handleClose(done: any) {
    this.$emit('update:visible', false)
  }

  private async handleSetUnFreeze() {
    (this.$refs.ruleForm as ElForm).validate(async(valid: boolean) => {
      if (valid) {
        try {
          this.unfreezeRequest.userId = this.userid
          await setUserUnfreeze(this.unfreezeRequest)
          this.$emit('update:visible', false)
          window.location.reload()
        } catch {
          this.$message.error('用户解冻操作失败！')
        }
      } else {
        return false
      }
    })
  }

  mounted() {
  }

  beforeDestroy() {
  }
}
