
























































































































import { Component, Vue, Prop } from 'vue-property-decorator'
import { setUserFreeze } from '@/api/manage-users'
import { ElForm } from 'element-ui/types/form'

export interface IUserFreezeRequest {
    userId: number;
    freezingTime: string;
    freezingRange: string;
    reasonsForFreezing: string;
    frozenRemarks: string;
}

@Component({
  name: 'UserFreezeDialog'
})
export default class extends Vue {
  @Prop() private visible!:Boolean
  @Prop() private userid!:number

  private validateFrozenRemarks = (
    rule: any,
    value: string,
    callback: Function
  ) => {
    // console.log(this.freezeRequest.reasonsForFreezing, value)
    if (this.freezeRequest.reasonsForFreezing === '其它' && value === undefined) {
      callback(new Error('冻结原因备注不能为空'))
    } else {
      callback()
    }
  }

  private freezeRequest:IUserFreezeRequest = {
    userId: this.userid,
    freezingTime: '1',
    freezingRange: '1',
    reasonsForFreezing: '内容被举报',
    frozenRemarks: ''
  }

  private formRules = {
    frozenRemarks: [{ validator: this.validateFrozenRemarks, trigger: 'blur' }]
  }

  // ui

  private handleClose(done: any) {
    this.$emit('update:visible', false)
  }

  private handleReasonsForFreezingChange() {
    (this.$refs.ruleForm as ElForm).resetFields()
  }

  private async handleSetFreeze() {
    (this.$refs.ruleForm as ElForm).validate(async(valid: boolean) => {
      if (valid) {
        try {
          this.freezeRequest.userId = this.userid
          await setUserFreeze(this.freezeRequest)
          this.$emit('update:visible', false)
          window.location.reload()
        } catch {
          this.$message.error('用户冻结操作失败！')
        }
      } else {
        return false
      }
    })
  }

  mounted() {
  }

  beforeDestroy() {
  }
}
