










































































































import { Component, Vue } from 'vue-property-decorator'
import { IBaseCityListEntity, IManageUserPageListEntity, IManageUserPageListItemEntity } from '@/api/types'
import { getCitys } from '@/api/base'
import { getUserPageList } from '@/api/manage-users'
import { getSexTitle, getUserStatusTitle, formatDate } from '@/utils/format'
import Pagination from '@/components/Pagination/index.vue'
import ImageButton from '@/components/ImageButton/index.vue'
import { ElCascader } from 'element-ui/types/cascader'
import NothingWidget from '@/components/Nothing/index.vue'
import { ElForm } from 'element-ui/types/form'

@Component({
  name: 'ManageUsersPage',
  components: {
    Pagination,
    ImageButton,
    NothingWidget
  }
})
export default class extends Vue {
  private searchData = {
    id: '',
    nickname: '',
    pagesize: 20,
    page: 1,
    city: ''
  }

  private dataCitys?: IBaseCityListEntity[] = []
  private dataTable: IManageUserPageListEntity = {
    items: []
  }
  private loading:boolean = false
  private lastLoginLocation = []

  private formRules = {
    id: [{ min: 0, max: 15, message: '长度在 0 到 15 个字符', trigger: 'blur' }],
    nickname: [{ min: 0, max: 15, message: '长度在 0 到 15 个字符', trigger: 'blur' }]
  }

  /// 生命周期

  mounted() {
    this.dataCitys = getCitys()
    this.doSearch()
  }

  beforeDestroy() {
  }

  /// 功能函数

  private getSexTitle = getSexTitle
  private getUserStatusTitle = getUserStatusTitle
  private formatDate = formatDate

  private async getList() {
    this.doSearch()
  }

  private doSearch() {
    (this.$refs.ruleForm as ElForm).validate(async(valid: boolean) => {
      if (valid) {
        this.loading = true
        const response = await getUserPageList({
          ...this.searchData
        })
        this.dataTable = response as IManageUserPageListEntity
        this.loading = false
      } else {
        return false
      }
    })
  }

  // ui

  private indexMethod(index:number) {
    return ((this.searchData.page - 1) * this.searchData.pagesize) + (index + 1)
  }

  private handleCityChange(val: any) {
    const selCity = this.$refs.city as any
    const node = selCity.getCheckedNodes()
    if (node !== null && node[0] !== undefined) {
      this.searchData.city = node[0].data.label
    } else {
      this.searchData.city = ''
    }
  }

  private async handleSearch() {
    this.searchData.page = 1
    this.doSearch()
  }

  private async handleShowDetail(row:IManageUserPageListItemEntity) {
    this.$router.push({ path: `/manage-users/detail/${row.id}` })
  }

  private handleFilterColumn(value: any, row: any, column: any) {
    // console.log(value, row, column)
    const property = column['property']
    return row[property] === value
  }
}
