


















import { Component, Vue, Prop } from 'vue-property-decorator'
import 'video.js/dist/video-js.css'
import './videos-player.css'
import { videoPlayer } from 'vue-video-player'
import { IManageVidoesPageListItemEntity } from '@/api/types'

export interface IPlayerOptions {
    sources?: ISource[];
    poster?: string;
}
export interface ISource {
    type?: string;
    src?: string;
}

@Component({
  name: 'VideoPlayerDialog',
  components: { videoPlayer }
})
export default class extends Vue {
  @Prop() private visible!:Boolean
  @Prop() private videoUrl!:string
  @Prop() private picUrl!:string

  private playerOptions:IPlayerOptions = {
    // muted: true,
    // autoplay: true,
    // height: '360',
    sources: [{
      type: 'video/mp4',
      src: this.videoUrl
    }],
    poster: this.picUrl
  }

  // ui

  private handleClose(done: any) {
    this.$emit('closed')
  }

  mounted() {
  }

  beforeDestroy() {
  }
}
