







































































































import { Component, Vue, Prop } from 'vue-property-decorator'
import { IManageVidoesPageListEntity, IManageVidoesPageListItemEntity } from '@/api/types'
import { getVideosPageList } from '@/api/manage-videos'
import { getSexTitle, getUserStatusTitle, formatDateSS } from '@/utils/format'
import Pagination from '@/components/Pagination/index.vue'
import ImageButton from '@/components/ImageButton/index.vue'
import VideosPlayer from './videos-player.vue'
import NothingWidget from '@/components/Nothing/index.vue'

@Component({
  name: 'VideosListWidget',
  components: {
    Pagination,
    ImageButton,
    VideosPlayer,
    NothingWidget
  }
})
export default class extends Vue {
  @Prop({ default: '' }) private uid!: string;

  private searchData = {
    uid: this.uid,
    page: 1,
    pagesize: 10,
    sortProp: 'createDate',
    sortOrder: 'descending'
  }

  private currentDialogComponent: string = ''
  private dialogVisible:boolean = false
  private videoUrl: string = ''
  private picUrl: string = ''

  private dataTable: IManageVidoesPageListEntity = {
    items: []
  }

  private getSexTitle = getSexTitle
  private getUserStatusTitle = getUserStatusTitle
  private formatDate = formatDateSS

  mounted() {
    this.ajaxGetPageList()
  }

  beforeDestroy() {
  }

  private indexMethod(index:number) {
    return ((this.searchData.page - 1) * this.searchData.pagesize) + (index + 1)
  }

  private async ajaxGetPageList() {
    const response = await getVideosPageList({
      ...this.searchData
    })
    this.dataTable = response as IManageVidoesPageListEntity
  }

  private async handleShowDetail(row:IManageVidoesPageListItemEntity) {
    this.videoUrl = row.videoUrl !== undefined ? row.videoUrl : ''
    this.picUrl = row.picUrl !== undefined ? row.picUrl : ''
    this.currentDialogComponent = 'VideosPlayer'
    this.dialogVisible = true
  }

  private handleDialogClosed() {
    this.currentDialogComponent = ''
    this.dialogVisible = false
  }

  private handleSortChange(value:any) {
    if (value.order != null && value.prop != null) {
      this.searchData.sortProp = value.prop
      this.searchData.sortOrder = value.order
    }
    this.ajaxGetPageList()
  }
}
