



















































































































import { Component, Vue, Prop } from 'vue-property-decorator'
import { IManageMessageDetailEntity, IManageMessagesCommentsPageListEntity, IManageMessagesCommentsPageListItemEntity } from '@/api/types'
import { getMessageDetail, setMessagesTop, setMessagesUnTop } from '@/api/manage-messages'
import { getCommentsPageList } from '@/api/manage-comments'
import { formatDate } from '@/utils/format'
import NothingWidget from '@/components/Nothing/index.vue'

@Component({
  name: 'MessageDetailDialog',
  components: {
    NothingWidget
  }
})
export default class extends Vue {
  @Prop() private visible!:Boolean

  private data?:IManageMessageDetailEntity = {}

  private searchData = {
    messageID: -1,
    page: 1,
    pagesize: 10,
    sortProp: 'id',
    sortOrder: 'descending'
  }
  private commentsList:IManageMessagesCommentsPageListEntity = {}
  private commentsListIsEnd:boolean = false
  private commentsLoading:boolean = false

  private formatDate = formatDate

  // 拉取数据

  public async getData(messageID: number) {
    this.searchData.messageID = messageID
    const response = await getMessageDetail(this.searchData.messageID)
    this.data = response as IManageMessageDetailEntity
    await this.getComments()
  }

  private async getComments() {
    if (this.commentsLoading) {
      return
    }
    this.commentsLoading = true
    const response = await getCommentsPageList({
      ...this.searchData
    })
    this.commentsLoading = false
    this.searchData.page += 1

    let tmpList = response as IManageMessagesCommentsPageListEntity
    if (this.commentsList.items === undefined) {
      this.commentsList = tmpList
    } else {
      if (tmpList.counts === 0) {
        this.commentsListIsEnd = true
      } else {
        this.commentsList.counts = tmpList.counts
        this.commentsList.pages = tmpList.pages
        this.commentsList.items = this.commentsList.items.concat(tmpList.items as IManageMessagesCommentsPageListItemEntity[])
      }
    }
  }

  // ui

  private handleClose(done: any) {
    this.$emit('update:visible', false)
  }

  private async handleMessageTop() {
    await setMessagesTop(this.searchData.messageID)
    await this.getData(this.searchData.messageID)
  }

  private async handleMessageUnTop() {
    await setMessagesUnTop(this.searchData.messageID)
    await this.getData(this.searchData.messageID)
  }

  mounted() {
  }

  beforeDestroy() {
  }
}
